.ditty-page {
  background: #fff;
  overflow: hidden;
}
.ditty-page #wpcontent {
  display: flex;
  flex-direction: row;
  padding-left: 0;
}
.ditty-page #wpbody {
  flex: 1;
}
.ditty-page #wpbody-content {
  padding: 0;
  float: none;
}

.ditty-button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 5px;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
  text-align: center;
  text-decoration: none;
  color: #23282d;
  background: rgba(0, 0, 0, 0.2);
  border: none;
  border-radius: 3px;
  padding: 10px 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  transition: color 0.25s ease, background-color 0.25s ease;
}
.ditty-button.is-primary {
  color: #fff;
  background: #19bf7c;
}
.ditty-button.is-small {
  font-size: 13px;
  padding: 5px 10px;
}
.ditty-button:disabled {
  cursor: default;
  opacity: 0.4;
}

.ditty-button-group {
  display: flex;
  gap: 10px;
}
.ditty-button-group.is-tabbed {
  align-items: stretch;
  justify-content: flex-start;
  background: #32373c;
  border-radius: 3px;
  overflow: hidden;
  gap: 1px;
}
.ditty-button-group.is-tabbed .ditty-button {
  border-radius: 0;
}

.ditty-filter {
  display: flex;
  align-items: flex-end;
  gap: 5px;
}
.ditty-filter__search {
  flex: 1;
}
.ditty-filter .ditty-button {
  height: 36px;
}
.ditty-filter .ditty-button.active {
  color: #fff;
  background: #19bf7c;
}

.ditty-icon-block {
  display: flex;
}
.ditty-icon-block__icon {
  flex: 0 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: #ccc;
  border-radius: 3px;
  margin-right: 10px;
}
.ditty-icon-block__icon svg {
  width: 40%;
  height: 40%;
  min-width: 15px;
  min-height: 15px;
  max-width: 25px;
  max-height: 25px;
}
.ditty-icon-block__contents {
  flex: 1;
}
.ditty-icon-block__contents h1,
.ditty-icon-block__contents h2,
.ditty-icon-block__contents h3,
.ditty-icon-block__contents h4,
.ditty-icon-block__contents h5,
.ditty-icon-block__contents h6,
.ditty-icon-block__contents p {
  margin: 0 0 5px;
}
.ditty-icon-block__contents > *:last-child {
  margin-bottom: 0;
}

.ditty-icon-button {
  height: 60px;
  min-width: 60px;
  flex: 0 0 auto;
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  color: rgba(29, 35, 39, 0.8);
  background: rgba(0, 0, 0, 0.02);
  border: none;
  border-radius: 3px;
  padding: 5px;
}
.ditty-icon-button:hover:not(.ditty-icon-button--active) {
  color: #1d2327;
  background: rgba(0, 0, 0, 0.05);
}
.ditty-icon-button--active {
  background: #19bf7c;
}
.ditty-icon-button--active .ditty-icon-button__icon {
  display: block;
  color: #fff;
}
.ditty-icon-button--active .ditty-icon-button__label {
  color: #fff;
}
.ditty-icon-button__icon {
  height: 20px;
  flex: 0 0 auto;
}
.ditty-icon-button__label {
  display: block;
  font-size: 11px;
  font-weight: 400;
  margin-top: 5px;
  flex: 0 0 auto;
}

.ditty-link {
  color: #323438;
  text-decoration: underline;
  border: none;
  background: transparent;
  padding: 0;
  cursor: pointer;
}
.ditty-link:hover {
  color: #1d2327;
}

.ditty-popup {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}
.ditty-popup__overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
}
.ditty-popup__contents {
  position: relative;
  width: calc(100% - 20px);
  height: calc(100% - 20px);
  display: flex;
  flex-direction: column;
  background: #fff;
  border-radius: 3px;
  margin-top: 10px;
  overflow: hidden;
}
.ditty-popup__header {
  border-bottom: 1px solid #ccc;
}
.ditty-popup__body {
  flex: 1;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
.ditty-popup__scroll {
  min-height: 100%;
}
.ditty-popup__footer {
  border-top: 1px solid #ccc;
}
.ditty-popup__footer .ditty-button-group {
  padding: 15px;
}
.ditty-popup--level-2 {
  z-index: 999;
}
.ditty-popup--level-2 .ditty-popup__contents {
  width: calc(100% - 40px);
  height: calc(100% - 40px);
  margin-top: 20px;
}
.ditty-popup--level-3 {
  z-index: 9999;
}
.ditty-popup--level-3 .ditty-popup__contents {
  width: calc(100% - 60px);
  height: calc(100% - 60px);
  margin-top: 30px;
}

#ditty-editor__adminbar {
  flex: 0 0 auto;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  padding: 20px;
  background: #fff;
  border-bottom: 1px solid #ccc;
  box-sizing: border-box;
}
#ditty-editor__adminbar * {
  box-sizing: border-box;
}
#ditty-editor__adminbar h2 {
  margin: 0;
}
#ditty-editor__adminbar__title {
  flex: 1;
}
#ditty-editor__adminbar button.ditty-has-updates {
  color: #fff;
  background: #19bf7c;
}